.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

.BrainhubCarouselItem {
  align-items: flex-start!important;
}

.advertSpace {
  width: 100%;
  margin-bottom: 3rem!important;
  padding: 3rem 0;
  text-align: center;
  border-radius: 10px;
  background-color: #f5f5f5;
}

.listFooter {
  margin-top: 0;
  list-style: none;
  font-family: 'Helvetica Light';
  font-weight: bold;
  font-size: 90%;
  padding: 0;
}

.listFooter li {
  margin-bottom: 1rem;
}

.loader {
  display: flex;
  position: relative;
  height: 100%;
  text-align: center;
  margin: auto;
}

.paperCont {
  margin: .5rem 0;
  padding: 0 .5rem;
}

.paperCont .MuiFormControl-root .MuiInputBase-formControl {
  font-size: 80%;
}

.paperCont .MuiInput-underline:after, .paperCont .MuiInput-underline:hover:not(.Mui-disabled):before, .paperCont .MuiFormControl-root .MuiInputBase-formControl:before {
  border: 0;
}

.commentGrid {
  margin-top: 2rem;
  max-height: 300px;
}

.textField {
  font-weight: bold;
  margin-top: 1rem;
  width: 75%;
}

.textField button {
  font-size: 75%;
  font-weight: bold;
  padding: 1rem;
  margin-right: -.9rem;
}

.textField .MuiInputBase-formControl {
  margin-top: 2rem;
  border-radius: 0;
}

.commentGrid::-webkit-scrollbar {
  width: 2.5px;
}

/* Track */
.commentGrid::-webkit-scrollbar-track {
  background: #f1f1f1; 
}
 
/* Handle */
.commentGrid::-webkit-scrollbar-thumb {
  background: #888; 
}

/* Handle on hover */
.commentGrid::-webkit-scrollbar-thumb:hover {
  background: #555; 
}

.navActive {
 color: #5ba56e!important; 
}